import React from "react";

import { burgerMenu } from "../assets/images/Header";

const HeaderMenuBurger = () => {
  return (
    <div className="menu__burger">
      <button className="menu">
        <img src={burgerMenu} alt="burger" />
      </button>
      <nav>
        <ul className="top__menu">
          <li>
            <a href="#features__block">features</a>
          </li>
          <li>
            <a href="#pricing__block">price</a>
          </li>
          <li>
            <a href="#works__block">our works</a>
          </li>
          <li>
            <a href="#team__block">team</a>
          </li>
          <li>
            <a href="#stats__block">stats</a>
          </li>
          <li>
            <a href="#testimonial__block">testimonial</a>
          </li>
          <li>
            <a href="#contacts__block">contacts</a>
          </li>
          <li>
            <a href="#callback__block">callback</a>
          </li>
        </ul>
      </nav>
    </div>
  );
};

export default HeaderMenuBurger;
