import * as aT from "./actionType";
import initialState from "./initialState.js";

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case aT.LOADED_HEADER_DATA:
      return { ...state, payload: action.payload };
    case aT.LOADED_HOME_PAGE_DATA:
      return { ...state, payload: action.payload };
    case aT.LOADED_TINYONE_PAGE_DATA:
      return { ...state, payload: action.payload };
    case aT.LOADED_OUR_TEAM_PAGE_DATA:
      return { ...state, payload: action.payload };
    case aT.LOADED_OUR_WORKS_PAGE_DATA:
      return { ...state, payload: action.payload };
    case aT.LOADED_CONTACTS_PAGE_DATA:
      return { ...state, payload: action.payload };
    default:
      return state;
  }
};

export default reducer;
